<script>
import { db } from "@/db";
import _ from "lodash";
import planSelector from "@/components/planSelector.vue";
export default {
  name: "addNewEmployee",
  components: {
    planSelector
  },
  props: ["showDialog", "testPlans"],
  data: () => ({
    planSelections: [],
    planSelValues: [],
    select: "",
    menu2: "",
    menu3: "",
    menu4: "",
    menu5: "",
    menu6: "",
    menu7: "",
    showSSN: false,
    step: 1,
    eventDate: new Date().toISOString().substr(0, 10),
    billingDate: new Date().toISOString().substr(0, 10),
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ],
    dateOfBirth: new Date().toISOString().substr(0, 10),
    stateList: [
      "Alabama",
      "Alaska",
      "American Samoa",
      "Arizona",
      "Arkansas",
      "California",
      "Colorado",
      "Connecticut",
      "Delaware",
