      "Iowa",
      "Kansas",
      "Kentucky",
      "Louisiana",
      "Maine",
      "Marshall Islands",
      "Maryland",
      "Massachusetts",
      "Michigan",
      "Minnesota",
      "Mississippi",
      "Missouri",
      "Montana",
      "Nebraska",
      "Nevada",
      "New Hampshire",
      "New Jersey",
      "New Mexico",
      "New York",
      "North Carolina",
      "North Dakota",
      "Northern Mariana Islands",
      "Ohio",
      "Oklahoma",
      "Oregon",
      "Palau",
      "Pennsylvania",
      "Puerto Rico",
      "Rhode Island",
      "South Carolina",
      "South Dakota",
      "Tennessee",
      "Texas",
      "Utah",
      "Vermont",
      "Virgin Island",
      "Virginia",
      "Washington",
      "West Virginia",
      "Wisconsin",
      "Wyoming"
    ],
    billEvents: ["Disability", "FMLA", "Absence"],
    newitemadd: {
      title: "",
      gender: "",
      first_name: "",
      middle_initial: "",
      last_name: "",
      ssn: "",
      dob: "",
      addr1: "",
      addr2: "",
      city: "",
      state: "",
      zip: "",
      email: "",
      home_phone: "",
      mobile_phone: "",
      bill_event: "",
      bill_start: "",
      event_date: "",
      bill_sum: "",
      status: "",
      plans: []
    },

    pendingClose: false,
    typingVal: false
  }),
  watch: {
    typingVal: function() {
      var self = this;
      if (!this.typingVal) {
        self.planSelValues = [];
        self.$refs.planSel.forEach(function(n) {
          self.planSelValues.push(Number(n.planValue));
        });
      }
    }
  },
  methods: {
    sumTotal: function(total, num) {
      return total + num;
    },

    handlePlanVal: function() {
      var self = this;
      self.typingVal = true;
      setTimeout(function() {
        self.typingVal = false;
      }, 1500);
    },
    advanceStep: function() {
      var self = this;
      if (self.step == 4) {
        self.planSelections = [];
        self.$refs.planSel.forEach(function(n) {
          self.planSelections.push({
            plan_type: n.planType,
            plan_title: n.planSelected,
            plan_value: n.planValue
          });
        });
        self.step++;
      } else {
        self.step++;
      }
    },
    closeAddNew: function() {
      var self = this;
      if (
        self.newitemadd.title != "" ||
        self.newitemadd.gender != "" ||
        self.newitemadd.first_name != "" ||
        self.newitemadd.middle_initial != "" ||
        self.newitemadd.last_name != "" ||
        self.newitemadd.ssn != "" ||
        self.newitemadd.dob != "" ||
        self.newitemadd.addr1 != "" ||
        self.newitemadd.addr2 != "" ||
        self.newitemadd.city != "" ||
        self.newitemadd.state != "" ||
        self.newitemadd.zip != "" ||
        self.newitemadd.email != "" ||
        self.newitemadd.home_phone != "" ||
        self.newitemadd.mobile_phone != "" ||
        self.newitemadd.bill_event != "" ||
        self.newitemadd.bill_start != "" ||
        self.newitemadd.event_date != "" ||
        self.newitemadd.bill_sum != "" ||
        self.newitemadd.status != ""
      ) {
        self.pendingClose = true;
      } else {
        self.closeAddNew2();
      }
    },
    closeAddNew2: function() {
      var self = this;
      self.$emit("addNewDialog", false);
      self.clearInput();
      self.pendingClose = false;
    },
    clearInput: function() {
      var self = this;
      self.newitemadd.title = "";
      self.newitemadd.gender = "";
      self.newitemadd.first_name = "";
      self.newitemadd.middle_initial = "";
      self.newitemadd.last_name = "";
      self.newitemadd.ssn = "";
      self.newitemadd.dob = "";
      self.newitemadd.addr1 = "";
      self.newitemadd.addr2 = "";
      self.newitemadd.city = "";
      self.newitemadd.state = "";
      self.newitemadd.zip = "";
      self.newitemadd.email = "";
      self.newitemadd.home_phone = "";
      self.newitemadd.mobile_phone = "";
      self.newitemadd.bill_event = "";
      self.newitemadd.bill_start = "";
      self.newitemadd.event_date = "";
      self.newitemadd.bill_sum = "";
      self.newitemadd.status = "";
      self.add_date_time = "";
      self.planSelections = [];
    },
    pushNew: function(r) {
      var self = this;
      db.collection("testEEs")
        .add({
          add_date_time: new Date(),
          title: self.newitemadd.title,
          gender: self.newitemadd.gender,
          first_name: self.newitemadd.first_name,
          middle_initial: self.newitemadd.middle_initial,
          last_name: self.newitemadd.last_name,
          ssn: self.newitemadd.ssn,
          dob: self.newitemadd.dob,
          addr1: self.newitemadd.addr1,
          addr2: self.newitemadd.addr2,
          city: self.newitemadd.city,
          state: self.newitemadd.state,
          zip: self.newitemadd.zip,
          email: self.newitemadd.email,
          home_phone: self.newitemadd.home_phone,
          mobile_phone: self.newitemadd.mobile_phone,
          bill_event: self.newitemadd.bill_event,
          bill_start: self.newitemadd.bill_start,
          event_date: self.newitemadd.event_date,
          bill_sum: self.sumPlans,
          status: r,
          planArray: self.chosenPlans
        })
        .then(function() {
          self.menu = false;
          self.closeAddNew2();
          self.step = 1;
        });
    },
    numberWithCommas: function(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  },
  computed: {
    sumPlans: function() {
      var self = this;
      if (self.planSelValues.length > 0) {
        return self.planSelValues.reduce(self.sumTotal);
      } else {
        return "N/A";
      }
    },
    sumTotalFormat: function() {
      var self = this;
      if (self.sumPlans > 0) {
        return "$" + self.numberWithCommas(self.sumPlans.toFixed(2));
      } else {
        return "N/A";
      }
    },
    availPlans: function() {
      var self = this;
      return _.groupBy(self.testPlans, "plan_type");
    },
    chosenPlans: function() {
      var self = this;
      return _.groupBy(self.planSelections, "plan_type");
    },
    currentTitle() {
      switch (this.step) {
        case 1:
          return "General Information";
        case 2:
          return "Address";
        case 3:
          return "Billable Events";
        case 4:
          return "Billable Benefits";
        case 5:
          return "Review";
        default:
          return "Confirmed";
      }
    }
  }
};
</script>
