<template>
  <v-container fluid>
    <v-row dense>
      <v-col>
        <v-select
          :disabled="disabled"
          v-model="planSelected"
          :items="planDetail"
          item-text="plan_name"
          item-value="plan_name"
          :label="planDetail[0].plan_type.charAt(0).toUpperCase() + planDetail[0].plan_type.substr(1) + '  Plans'"
        ></v-select>
      </v-col>
      <v-col cols="4">
        <v-text-field
          :disabled="disabled"
          prepend-inner-icon="mdi-currency-usd"
          v-model="planValue"
          :label="planDetail[0].plan_type.charAt(0).toUpperCase() + planDetail[0].plan_type.substr(1) + ' Employee Share'"
        ></v-text-field>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "planSelector",
  props: ["planDetail", "disabled"],
  data: () => ({
    planSelected: "",
    planValue: "",
    planType: ""
  }),
  watch: {
    planSelected: function() {
      var self = this;
      self.$emit("planSel", self.planSelected, self.planDetail[0].plan_type);
    },
    planValue: function() {
      var self = this;
      self.$emit("planVal", self.planValue, self.planDetail[0].plan_type);
    }
  },
  created: function() {
    var self = this;
    self.planType = self.planDetail[0].plan_type;
  }
};
</script>

